.home-heading {
	font-size: 1.7em;
	font-weight: bold;
}

.tag-category-heading {
	@extend .home-heading;
	font-variant: small-caps;
}

.posts {
	&-list {
		list-style: none;
		flex-grow: 1;
	}
}

.post {
	&-preview {
		padding-bottom: 20px;
	}

	&-list a {
		display: flex;
		justify-content: space-between;
		align-items: baseline;
		text-decoration: none;
	}

	&-title {
		font-size: 1.1rem;
		text-decoration: underline;
	}

	&-meta {
		font-style: italic;
		.slant {
			font-style: normal;
		}
		.meta-sep {
			padding: 0 10px;
		}
	}
}

blockquote {
	font-style: italic;
	color: hsl(0, 5%, 25%);
	p {
		text-align: center;
		position: relative;
	}
	p:first-of-type::before {
		content: "\201C";
		font-size: 60px;
		display: block;
		position: absolute;
		top: -30px;
		left: -50px;
		color: hsl(0, 5%, 25%);
	}
	margin-bottom: 30px; // slightly increase this compared to the top so it looks natural
}

// special stuff for first paragraph and letter
article > p:first-of-type {
	margin-top: 2em;
	text-indent: 0;
	text-align: justify;
}
article > p:first-of-type::first-letter {
	font-weight: 100;
	font-size: 4.0rem;
	float: left;
	padding-right: 6px;
}

.menu {
	margin: 0 0 1.5rem;

	&-ulist {
		display: flex;
		justify-content: center;
		list-style-type: none;
		margin: 0;
		padding: 0;
		overflow: hidden;
	}
	
	&-list {
		display: inline;
		text-align: center;
		list-style-type: none;
		margin: 0;
		padding: 0;
	}

	&-links {
		padding: 14px 16px;
	}
}

footer {
	line-height: 0.9;
	margin: 80px 0 0;
	.credits {
		text-align: center;
		font-style: italic;
	}
}

.pager {
	list-style: none;
	text-align: center;
	margin: 20px 0 0;
	padding-left: 0;
}

.pager ul {
	display: block;
}

.pager .previous > a {
	margin: 20px 0 0;
	float: left;
	display: block;
}

.pager .next > a {
	margin: 20px 0 0;
	float: right;
	display: block;
}

// style footnotes
.footnote-ref {
	font-size: 0.825rem;
	font-weight: bold;
	text-decoration: none;

	&:before {
		content: "[";
	}
	&:after {
		content: "]";
	}
}

.footnote-backref {
	text-decoration: none;
	border-bottom: 1px dashed;
}

#TableOfContents {
	margin-bottom: 1em;
	ol {
		list-style-type: lower-alpha;
	}
}

// support hugo builtin image shortcode
figcaption {
	text-align: right;
	p {
		font-size: 1.125rem;
		font-style: italic;
		text-align: right;
		margin-top: 0.5rem;
	}
	
}

.boxed {
	border: 1px solid;
	&-container {
		margin: 0;
		padding: 1.0rem;
	}
	&-label {
		font-style: italic;
		font-weight: bold;
		margin-right: 20px;
	}
	&-info {
		float: left;
		width: 4.5rem;
	}
	&-text {
		margin-left: 4.5rem;
	}
}

.pquote {
  color: #433d3d;
  padding-left: 2.5rem;
  margin-left: 0.5rem;
  border-left: 3px #433d3d solid;
  &-attrib {
    margin-top: 1.2em;
    margin-left: 2.5em;
    font-style: italic;
    font-weight: bold;
    &::before {
      content: '—';
    }
  }
}

// make all 'abstract' headings italic and make the spacing closer
#abstract {
	font-weight: normal;
	font-style: italic;
	margin-bottom: -20px;
}
